 <!--商品分类列表-->
<template>
  <div class="cms-list-layout">
    <!-- 不同平台切换 -->
    <cms-prompt>
      <li>列表展示所有文章分类</li>
    </cms-prompt>
    <cms-sticky>
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" @click="$routerLink('/articleCategory/save','save',{nodeIds:'0'})">添加分类</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" :row-style="showRow" style="width:100%" v-loading='loading'>
      <el-table-column label="id" prop="id" width="60"></el-table-column>
      <el-table-column label="分类名称" min-width="240">
        <template slot-scope="scope">
          <div class="menu-name" :style="{marginLeft:(scope.row._level*16)+'px'}" v-if="iconShow(scope.$index,scope.row)" @click="toggleExpanded(scope.$index)">
            <span class="tree-ctrl">
              <i v-if="scope.row._expanded" class="el-icon-caret-right"></i>
              <i v-else class="el-icon-caret-bottom"></i>
            </span>
            <span>{{scope.row.name}}</span>
          </div>
          <div v-else :style="{marginLeft:(scope.row._level*16)+'px'}">
            <span class="icon-dot">○</span>
            <span>{{scope.row.name}}</span>
          </div>
        </template>
      </el-table-column>
       <el-table-column label="排序" prop="sortNum">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sortNum" @keyup.enter.native="sortNum($event,scope.row.id)"> </el-input>
        </template>
      </el-table-column>
      <el-table-column label="显示在底部" prop="show">
        <template slot-scope="scope">
          <cms-switch v-model="scope.row.bottomShow" :id="scope.row.id"  @change="toggleVisable"></cms-switch>
        </template>
      </el-table-column>
      <el-table-column label="描述" prop="description" min-width="220"></el-table-column>
      <el-table-column label="操作" width='280' fixed="right" align="center">
        <template slot-scope="scope">
          <cms-button   v-prem="'/articleCategory/edit'"  icon='edit' size='small' type='info' @click="$routerLink('/articleCategory/edit','update',{id:scope.row.id,nodeIds:scope.row.nodeIds.toString()})">编辑</cms-button>
          <cms-button   v-prem="'/articleCategory/delete'"   icon='delete' size='small' type='info' @click.stop="deleteData(scope.row,scope.$index)">删除</cms-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import treeToArray from './../../../../utils/eval.js'
import {
  artCategoryGetChild,
  artCategoryDelete,
  artCategorySort,
  artCategoryShowBottom,
  artCategoryHideBottom
} from './../../../../api/article/articleCategory.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  data() {
    return {
      typeList: [],
      tableData: [],
      expandAll: false,
      queryParams: {
        parentId: '',
        childs: {
          parentId: ''
        }
      },
      selfId: 0, // 当前点击的id 不可删除
      columnState: true // 判断行隐藏
    }
  },
  methods: {
    toggleVisable(status, id) {
      if (status) {
        artCategoryShowBottom({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      } else {
        artCategoryHideBottom({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      }
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          artCategorySort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    },
    showRow: function(row) {
      const show = row.row._show
      row.row._show = show
      return show
        ? 'animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;'
        : 'display:none;'
    },
    // 切换下级是否展开
    toggleExpanded: function(trIndex) {
      const record = this.tableData[trIndex]
      if (record._expanded) {
        // 判断展开
        if (!record._load) {
          // 判断是否加载过
          this.queryParams.parentId = record.id
          this.loading = true
          artCategoryGetChild(this.queryParams).then(res => {
            if (res.code === 200) {
              const arr = treeToArray(res.data)
              for (let i = 0; i < arr.length; i++) {
                this.tableData.splice(trIndex + (i + 1), 0, arr[i])
              }
              record._expanded = false
              record._load = true
              this.loading = false
            }
          })
        } else {
          this.chidView(record, true)
          record._expanded = false
        }
      } else {
        record._expanded = true
        this.chidView(record, false)
      }
    },
    chidView(record, state) {
      // 子集显示，核心判断这个方法
      const arr = []
      Array.from(this.tableData).forEach(v => {
        if (v.nodeIds.indexOf(record.id) > -1 && v.id !== record.id) {
          v._show = state
        }
        arr.push(v)
      })
      this.tableData = arr
    },
    // 图标显示
    iconShow(index, record) {
      return !record.isChild
    },
    getTableData(callBack, params) {
      callBack(params).then(res => {
        if (res.code === 200) {
          this.tableData = treeToArray(res.data)
          this.loading = false
        }
      })
    },
    deleteData(row, index) {
      // 删除菜单
      this.$confirm('删除分类可能会影响系统正常运行,请确定是否删除!', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      })
        .then(() => {
          this.loading = true
          artCategoryDelete({ ids: [row.id] }).then(res => {
            this.loading = false
            if (res.code === 200) {
              if (row.isChild) {
                this.tableData.splice(index, 1)
              } else {
                this.queryParams.parentId = ''
                this.getTableData(artCategoryGetChild, this.queryParams)
              }
              this.$successMessage('删除成功')
            } else {
              this.$errorMessage('删除失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    getTypeList() {
      this.getTableData(artCategoryGetChild, this.queryParams)
    }
  },
  created() {
    this.loading = false
    this.getTypeList()
  }
}
</script>
<style rel="stylesheet/css">
</style>
<style lang="scss" rel="stylesheet/scss" scoped>
$color-blue: #188ae2;

.menu-name {
  position: relative;
  cursor: pointer;
  user-select: none;
}
.tree-ctrl {
  cursor: pointer;
  color: $color-blue;
  margin-left: 0px;
}
.icon-dot {
  width: 14px;
  height: 14px;
  display: inline-block;
  position: relative;
  top: 1px;
  left: 4px;
  color: $color-blue;
  font-size: 18px;
  font-weight: bold;
}
</style>



